
import { defineComponent } from 'vue'
import AppButton from './ui/AppButton.vue'

export default defineComponent({
  data () {
    return {
      inputData: ''
    }
  },
  emits: ['sendReq'],
  methods: {
    btnHandler () {
      this.$emit('sendReq', this.inputData)
      this.inputData = ''
    }
  },
  components: {
    AppButton
  },
  props: ['error']

})
